/* üîπ Glob√°ln√≠ nastaven√≠ */
body {
    font-family: Arial, sans-serif;
    background-color: #ffffff;
    color: #000000;
    margin: 0;
    padding: 0;
  }
  
  /* üîπ Horn√≠ li≈°ta */
  .header {
    background-color: #414141;  /* ≈°ed√° barva */
    color: white;
    text-align: center;
    font-size: 28px;
    font-weight: bold;
    padding: 20px 0;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  }
  
  /* üîπ Spodn√≠ li≈°ta */
  .footer {
    background-color: #ffc15e;
    color: rgb(0, 0, 0);
    text-align: center;
    padding: 15px 0;
    font-size: 18px;
    position: absolute;
    width: 100%;
    bottom: 0;
  }
  
  /* üîπ Tlaƒç√≠tka */
  .button {
    background-color: #cbcbcb;
    color: hsl(0, 0%, 0%);
    border: none;
    padding: 12px 24px;
    margin: 10px;
    font-size: 18px;
    font-weight: bold;
    cursor: pointer;
    border-radius: 8px;
    transition: 0.3s;
  }
  
  .button:hover {
    background-color: #ffc15e;
  }
  
  /* üîπ Seznam event≈Ø */
  .events-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 20px;
  }
  
  .event-card {
    background-color: white;
    width: 80%;
    max-width: 600px;
    padding: 15px;
    margin: 10px 0;
    border-radius: 8px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
    cursor: pointer;
    transition: 0.3s;
  }
  
  .event-card:hover {
    background-color: #fff5e4;
  }
  
  /* üîô Tlaƒç√≠tko zpƒõt */
.back-button {
    background-color: #414141;
    color: white;
    border: none;
    padding: 10px 20px;
    font-size: 16px;
    font-weight: bold;
    cursor: pointer;
    border-radius: 8px;
    transition: 0.3s;
    margin-bottom: 20px;
  }
  
  .back-button:hover {
    background-color: #ffc15e;
  }
  
  /* üèÜ N√°zev eventu */
  .event-title {
    font-size: 35px;
    font-weight: bold;
    text-align: center;
    margin-bottom: 10px;
  }
  
  /* üìÖ Datum eventu */
  .event-date {
    font-size: 20px;
    text-align: center;
    color: #555;
    margin-bottom: 20px;
  }
  
  /* üìÑ Event Schedule */
  .event-schedule {
    display: block;
    text-align: center;
    font-size: 18px;
    font-weight: bold;
    color: #154a82;
    text-decoration: none;
    margin-bottom: 20px;
  }
  
  .event-schedule:hover {
    text-decoration: underline;
  }
  
  /* üìå Dny eventu */
  .event-day {
    margin-top: 30px;
    
  }
  
  .day-title {
    font-size: 20px;
    font-weight: bold;
    text-decoration: underline;
    color: #444;
    margin-bottom: 10px;
    padding: 5px 5px;
  }
  
  /* üìå Seznam discipl√≠n */
  .disciplines-list {
    display: flex;
    flex-direction: column;
    gap: 10px;
  }
  
  .discipline-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: white;
    padding: 12px 25px;
    border-radius: 8px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  }
  
  .discipline-name {
    font-size: 18px;
    font-weight: bold;
  }
  
  .discipline-links {
    display: flex;
    gap: 15px;
  }
  
  .discipline-link {
    font-size: 16px;
    font-weight: bold;
    color: #154a82;
    text-decoration: none;
  }
  
  .discipline-link:hover {
    text-decoration: underline;
  }
  /* üîô Tlaƒç√≠tko zpƒõt */
.back-button {
    background-color: #414141;
    color: white;
    border: none;
    padding: 10px 18px;
    font-size: 16px;
    font-weight: bold;
    cursor: pointer;
