  /* üìä Tabulka v√Ωsledk≈Ø */
  /* üìä Tabulka v√Ωsledk≈Ø ‚Äì upraven√° */
.discipline-table {
    width: 90%;  /* ‚úÖ Zmen≈°√≠ tabulku a p≈ôid√° okraje */
    max-width: 1200px; /* ‚úÖ Maxim√°ln√≠ ≈°√≠≈ôka tabulky */
    margin: 0 auto; /* ‚úÖ Zarovn√°n√≠ na st≈ôed */
    border-collapse: collapse;
    margin-top: 15px;
  }
  
  /* üìä Sloupce ‚Äì p≈ôizp≈Øsoben√≠ ≈°√≠≈ôky */
  .discipline-table th:nth-child(1),  /* Rnk */
  .discipline-table td:nth-child(1) {
    width: 50px; /* ‚úÖ Pevn√° ≈°√≠≈ôka pro Rank */
  }
  
  .discipline-table th:nth-child(2),  /* Bib */
  .discipline-table td:nth-child(2) {
    width: 70px; /* ‚úÖ Pevn√° ≈°√≠≈ôka pro startovn√≠ ƒç√≠slo */
  }



  .discipline-table th:nth-child(5),  /* Country */
  .discipline-table td:nth-child(5) {
    width: 80px; /* ‚úÖ Pevn√° ≈°√≠≈ôka pro Country */
  }

  /* üìå Prvn√≠ sloupec v tabulce bude v≈ædy tuƒçn√Ω */
.discipline-table th:first-child,
.discipline-table td:first-child {
  font-weight: bold;
}

  
  /* üìä Ostatn√≠ sloupce se p≈ôizp≈Øsob√≠ automaticky */
  
  
  .discipline-table th,
  .discipline-table td {
    border: 1px solid #ddd;
    padding: 10px;
    text-align: center;
  }
  
  .discipline-table th {
    background-color: #313131;
    color: rgb(255, 255, 255);
    font-weight: bold;
  }
  
  /* üîπ Tuƒçn√© ≈ô√°dky v tabulce */
  .bold-row {
    font-weight: bold;
    background-color: #f8f9fa;
  }
  
  /* ‚ùå Zpr√°va o nedostatku dat */
  .no-data {
    text-align: center;
    color: red;
    font-size: 18px;
    margin-top: 20px;
  }
  
  /* üîπ Vizu√°ln√≠ mezery pro pr√°zdn√© ≈ô√°dky */
.spacer-row td {
    height: 12px; /* ‚úÖ Nastav men≈°√≠ v√Ω≈°ku pro bƒõ≈æn√© mezery */
    border: none; /* ‚úÖ Odstran√≠me ohraniƒçen√≠ */
    background: transparent; /* ‚úÖ ≈Ω√°dn√© pozad√≠ */
  }
  
  /* üîπ Vƒõt≈°√≠ mezera, pokud jsou pr√°zdn√© ≈ô√°dky po sobƒõ */
  .spacer-row + .spacer-row td {
    height: 3px; /* ‚úÖ Vƒõt≈°√≠ mezera, pokud n√°sleduje dal≈°√≠ pr√°zdn√Ω ≈ô√°dek */
  }
  
  /* üîπ Zv√Ωraznƒõn√≠ slova WINNER */
.winner-cell {
    color: #26a526; /* ‚úÖ Zelen√Ω text */
    font-weight: bold; /* ‚úÖ Tuƒçn√© p√≠smo */
    text-transform: uppercase; /* ‚úÖ Velk√° p√≠smena */
    text-align: center;
  }
  /* üî≥ Li≈°ty "Final for Bronze/Gold" */

  
  
  /* üèÅ Hlaviƒçkov√Ω ≈ô√°dek s "Rnk, Bib, Last Name..." */
  thead .header-row {
