/* ‚úÖ Tabulka pro Omnium se rozt√°hne p≈ôes cel√Ω prostor */
.omnium-table {
    width: 100% !important;
    max-width: none !important;
    table-layout: auto !important; /* automatick√© rozlo≈æen√≠ sloupc≈Ø */
    word-wrap: break-word; /* zalamov√°n√≠ slov */
    white-space: normal; /* povolen√≠ zalomen√≠ textu */
}

/* ‚úÖ Povolen√≠ zalamov√°n√≠ slov ve v≈°ech bu≈àk√°ch Omnium */
.omnium-table th, 
.omnium-table td {
    white-space: normal;
}

/* ‚úÖ Specifick√° ≈°√≠≈ôka pro sloupce Omnium Points */
